import React from 'react';

function useClickOutside(ref, fn) {
  function handleClickOutside(event) {
    if (ref && ref.current && !ref.current.contains(event.target)) {
      fn();
    }
  }

  React.useEffect(function () {
    if (ref) {
      document.addEventListener("click", handleClickOutside);
    }

    return function () {
      if (ref) {
        document.removeEventListener("click", handleClickOutside);
      }
    };
  });
}

var mousenavigation = false;

function handleMouseDown() {
  if (!mousenavigation) {
    mousenavigation = true;
    var htmlElement = document.querySelector("html");
    htmlElement && htmlElement.setAttribute("data-mousenavigation", "true");
    document.removeEventListener("mousedown", handleMouseDown);
    document.addEventListener("keydown", handleKeydown);
  }
}

function handleKeydown(event) {
  if (event.key === "Tab") {
    if (mousenavigation) {
      mousenavigation = false;
      var htmlElement = document.querySelector("html");
      htmlElement && htmlElement.removeAttribute("data-mousenavigation");
      document.removeEventListener("keydown", handleKeydown);
      document.addEventListener("mousedown", handleMouseDown);
    }
  }
}

function initTabListener() {
  if (document) {
    document.addEventListener("mousedown", handleMouseDown);
  }
}

export { initTabListener, useClickOutside };
